* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow: hidden;
  background: #1a1a2e;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Menu Screens */
#menu-screen, #game-over-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  z-index: 100;
}

.menu-container {
  text-align: center;
  color: white;
}

.menu-container h1 {
  font-size: 4rem;
  margin-bottom: 2rem;
  text-shadow: 0 0 20px rgba(255, 200, 100, 0.5);
  color: #ffd700;
  letter-spacing: 0.3em;
}

.menu-container button {
  padding: 1rem 3rem;
  font-size: 1.5rem;
  background: linear-gradient(180deg, #4a7c59 0%, #2d5a3d 100%);
  color: white;
  border: 3px solid #6b8e23;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.menu-container button:hover {
  background: linear-gradient(180deg, #5a8c69 0%, #3d6a4d 100%);
  transform: scale(1.05);
  box-shadow: 0 0 30px rgba(107, 142, 35, 0.6);
}

.menu-container p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  color: #ccc;
}

/* Game Container */
#game-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#game-canvas {
  display: block;
  width: 100%;
  height: 100%;
}

/* HUD */
#hud {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

/* Crosshair */
#crosshair {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 30px;
  color: rgba(255, 50, 50, 0.9);
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.8);
  font-weight: bold;
}

/* Health Bar */
#health-bar {
  position: absolute;
  bottom: 30px;
  left: 30px;
  width: 250px;
  height: 30px;
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid #444;
  border-radius: 5px;
  overflow: hidden;
}

#health-fill {
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #ff4444 0%, #44ff44 100%);
  background: #44ff44;
  transition: width 0.3s ease;
}

#health-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: bold;
  text-shadow: 1px 1px 2px black;
}

/* Reload Indicator */
#reload-indicator {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translateX(-50%);
  color: #ffcc00;
  font-size: 1.2rem;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
}

/* Minimap */
#minimap {
  position: absolute;
  bottom: 30px;
  right: 30px;
  width: 200px;
  height: 200px;
  background: rgba(0, 0, 0, 0.7);
  border: 2px solid #666;
  border-radius: 5px;
}

#minimap canvas {
  width: 100%;
  height: 100%;
}
